.navigation-header {
  width: 100%;
}

.navigation-header-wrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.navigation-header-image {
  height: 75px;
  cursor: pointer;
}

.navigation-header-content {
  text-align: center;
  width: 100%;
}

.navigation-header-content p {
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 33px;
  color: #005665;
}