.card {
  text-align: center;
  width: 100%;
  max-width: 300px;
}

.card__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  border-radius: 20px;
  padding: 10px 0;
}

.gray {
  background: #C3D0D2;
}

.white {
  background: #FFFFFF;
}

.card__label {
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
  color: #3D5054;
}

.card__label-white {
  display: inline-block;
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  color: #005665;
  margin: 0 0 10px 0;
}

.card__text {
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  line-height: 26px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #3D5054;
  margin: 0;
}

.white .card__text {
  font-family: Roboto, monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
  color: #7C939C;
}

.helperText {
  color: red;
}